import test from 'ava'
var unity = require('@locational/unity')
const store = unity.Store()

test('Store Register and lookup Value',t => {

  let _store = new store()
  _store.registerValue('number_value',9)
  let actual = 9;
  let expected = _store.lookupValue('number_value')
  t.deepEqual(actual, expected)  
})